.yJabTa_table {
  border-collapse: separate;
  border-spacing: 0;
  --color-background: var(--color-white);
  --color-table-border: var(--color-stone-200);
  --color-table-cell-contrast: var(--color-stone-100);
  --color-table-cell-hover: var(--color-blue-50);
  width: 100%;
  font-size: 14px;
}

.yJabTa_tableHeader {
  z-index: 2;
  position: sticky;
  top: -1px;
}

.yJabTa_tableTopCell {
  border-top: 1px solid var(--color-table-border);
  text-align: center;
}

th, td {
  border-right: 1px solid var(--color-table-border);
  border-bottom: 1px solid var(--color-table-border);
}

tr th:first-child, tr td:first-child {
  border-left: 1px solid var(--color-table-border);
}

thead tr:nth-child(2) th {
  z-index: 1;
  position: sticky;
  top: 0;
}

tr td:first-child {
  z-index: 1;
  position: sticky;
  left: -1px;
}

th, td {
  text-align: right;
  white-space: nowrap;
  background: #fff;
  padding: 10px;
  overflow: visible;
}

tbody tr:nth-child(2n) td {
  background: var(--color-table-cell-contrast);
}

tbody tr:hover td {
  background: var(--color-table-cell-hover);
}
